.popup {
  width: 100%;
  min-height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  visibility: hidden;
  align-items: center;
  justify-content: center;
  z-index: 10;
  background-color: black;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: all 0.25s linear;
}